'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Checkbox } from '@/components/ui/checkbox'
import { Separator } from '@/components/ui/separator'

interface StudentFormData {
  fullName: string
  personalPhone: string
  fatherPhone: string
  motherPhone: string
  dateOfBirth: string
  academicGrade: string
  academicStream: string
  schoolName: string
  selectedInstructors: string[]
}

interface Instructor {
  id: string
  name: string
  subjects: string[]
}

export default function NewStudentPage() {
  const router = useRouter()
  const [currentStep, setCurrentStep] = useState(1)
  const [isLoading, setIsLoading] = useState(false)
  const [qrCodeUrl, setQrCodeUrl] = useState<string | null>(null)
  const [studentId, setStudentId] = useState<string | null>(null)

  const [formData, setFormData] = useState<StudentFormData>({
    fullName: '',
    personalPhone: '',
    fatherPhone: '',
    motherPhone: '',
    dateOfBirth: '',
    academicGrade: '',
    academicStream: '',
    schoolName: '',
    selectedInstructors: []
  })

  // Mock instructors data
  const instructors: Instructor[] = [
    { id: '1', name: 'Dr. Mohamed Ali', subjects: ['Mathematics', 'Physics'] },
    { id: '2', name: 'Prof. Sarah Ahmed', subjects: ['Chemistry', 'Biology'] },
    { id: '3', name: 'Mr. Omar Khaled', subjects: ['English', 'Literature'] },
    { id: '4', name: 'Dr. Amina Hassan', subjects: ['Arabic', 'History'] },
    { id: '5', name: 'Prof. Youssef Ibrahim', subjects: ['Geography', 'Social Studies'] },
    { id: '6', name: 'Dr. Khaled Nasser', subjects: ['Mathematics', 'Statistics'] },
    { id: '7', name: 'Prof. Mona Farid', subjects: ['Physics', 'Chemistry'] },
    { id: '8', name: 'Mr. Ahmed Samir', subjects: ['Computer Science', 'Mathematics'] }
  ]

  const handleInputChange = (field: keyof StudentFormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const handleInstructorToggle = (instructorId: string) => {
    setFormData(prev => ({
      ...prev,
      selectedInstructors: prev.selectedInstructors.includes(instructorId)
        ? prev.selectedInstructors.filter(id => id !== instructorId)
        : [...prev.selectedInstructors, instructorId]
    }))
  }

  const generateQRCode = async (studentId: string) => {
    // Simulate QR code generation
    const qrData = `STUDENT_ID:${studentId}|NAME:${formData.fullName}|PHONE:${formData.personalPhone}`
    
    // In a real implementation, you would call an API to generate the QR code
    // For now, we'll create a mock QR code URL
    const mockQrUrl = `data:image/svg+xml;base64,${btoa(`
      <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect width="200" height="200" fill="white"/>
        <rect x="20" y="20" width="160" height="160" fill="black"/>
        <rect x="40" y="40" width="120" height="120" fill="white"/>
        <text x="100" y="105" text-anchor="middle" font-family="Arial" font-size="12" fill="black">QR Code</text>
        <text x="100" y="125" text-anchor="middle" font-family="Arial" font-size="8" fill="black">${studentId}</text>
      </svg>
    `)}`
    
    return mockQrUrl
  }

  const handleSubmit = async () => {
    if (formData.selectedInstructors.length < 5) {
      alert('Please select at least 5 instructors')
      return
    }

    setIsLoading(true)
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // Generate student ID
      const newStudentId = `STU${Date.now()}`
      setStudentId(newStudentId)
      
      // Generate QR code
      const qrUrl = await generateQRCode(newStudentId)
      setQrCodeUrl(qrUrl)
      
      setCurrentStep(4) // Move to success step
    } catch (error) {
      alert('Registration failed. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const downloadQRCode = () => {
    if (qrCodeUrl) {
      const link = document.createElement('a')
      link.href = qrCodeUrl
      link.download = `${formData.fullName.replace(/\s+/g, '_')}_QR_Code.svg`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    }
  }

  const renderStep = () => {
    switch (currentStep) {
      case 1:
        return (
          <div className="space-y-6">
            <div className="text-center mb-6">
              <h2 className="text-2xl font-bold text-slate-900">Personal Information</h2>
              <p className="text-slate-600">Enter the student's personal details</p>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="fullName">Full Name *</Label>
                <Input
                  id="fullName"
                  value={formData.fullName}
                  onChange={(e) => handleInputChange('fullName', e.target.value)}
                  placeholder="Enter full name"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="personalPhone">Personal Phone *</Label>
                <Input
                  id="personalPhone"
                  value={formData.personalPhone}
                  onChange={(e) => handleInputChange('personalPhone', e.target.value)}
                  placeholder="+20 123 456 7890"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="fatherPhone">Father's Phone *</Label>
                <Input
                  id="fatherPhone"
                  value={formData.fatherPhone}
                  onChange={(e) => handleInputChange('fatherPhone', e.target.value)}
                  placeholder="+20 123 456 7890"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="motherPhone">Mother's Phone *</Label>
                <Input
                  id="motherPhone"
                  value={formData.motherPhone}
                  onChange={(e) => handleInputChange('motherPhone', e.target.value)}
                  placeholder="+20 123 456 7890"
                  required
                />
              </div>
              
              <div className="space-y-2 md:col-span-2">
                <Label htmlFor="dateOfBirth">Date of Birth *</Label>
                <Input
                  id="dateOfBirth"
                  type="date"
                  value={formData.dateOfBirth}
                  onChange={(e) => handleInputChange('dateOfBirth', e.target.value)}
                  required
                />
              </div>
            </div>
          </div>
        )

      case 2:
        return (
          <div className="space-y-6">
            <div className="text-center mb-6">
              <h2 className="text-2xl font-bold text-slate-900">Academic Information</h2>
              <p className="text-slate-600">Enter academic details and school information</p>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="academicGrade">Academic Grade *</Label>
                <Select value={formData.academicGrade} onValueChange={(value) => handleInputChange('academicGrade', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select grade" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="10">Grade 10</SelectItem>
                    <SelectItem value="11">Grade 11</SelectItem>
                    <SelectItem value="12">Grade 12</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="academicStream">Academic Stream *</Label>
                <Select value={formData.academicStream} onValueChange={(value) => handleInputChange('academicStream', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select stream" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Science">Science</SelectItem>
                    <SelectItem value="Math">Mathematics</SelectItem>
                    <SelectItem value="Literature">Literature</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="space-y-2 md:col-span-2">
                <Label htmlFor="schoolName">School Name *</Label>
                <Input
                  id="schoolName"
                  value={formData.schoolName}
                  onChange={(e) => handleInputChange('schoolName', e.target.value)}
                  placeholder="Enter school name"
                  required
                />
              </div>
            </div>
          </div>
        )

      case 3:
        return (
          <div className="space-y-6">
            <div className="text-center mb-6">
              <h2 className="text-2xl font-bold text-slate-900">Select Instructors</h2>
              <p className="text-slate-600">Choose at least 5 instructors for this student</p>
              <div className="mt-2">
                <span className="text-sm font-medium text-blue-600">
                  Selected: {formData.selectedInstructors.length}/5+ instructors
                </span>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {instructors.map((instructor) => (
                <Card key={instructor.id} className={`cursor-pointer transition-all duration-200 ${
                  formData.selectedInstructors.includes(instructor.id)
                    ? 'border-blue-500 bg-blue-50'
                    : 'border-slate-200 hover:border-slate-300'
                }`}>
                  <CardContent className="p-4">
                    <div className="flex items-start space-x-3">
                      <Checkbox
                        checked={formData.selectedInstructors.includes(instructor.id)}
                        onCheckedChange={() => handleInstructorToggle(instructor.id)}
                      />
                      <div className="flex-1">
                        <h3 className="font-medium text-slate-900">{instructor.name}</h3>
                        <p className="text-sm text-slate-600 mt-1">
                          {instructor.subjects.join(', ')}
                        </p>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        )

      case 4:
        return (
          <div className="text-center space-y-6">
            <div className="w-16 h-16 bg-green-100 rounded-full mx-auto flex items-center justify-center mb-4">
              <div className="w-8 h-8 bg-green-500 rounded-full"></div>
            </div>
            
            <h2 className="text-2xl font-bold text-green-600">Registration Successful!</h2>
            <p className="text-slate-600">
              {formData.fullName} has been successfully registered with ID: <strong>{studentId}</strong>
            </p>
            
            {qrCodeUrl && (
              <div className="bg-white p-6 rounded-lg border-2 border-slate-200 inline-block">
                <img src={qrCodeUrl} alt="Student QR Code" className="w-48 h-48 mx-auto" />
                <p className="text-sm text-slate-600 mt-2">Student QR Code</p>
              </div>
            )}
            
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button onClick={downloadQRCode} className="bg-blue-500 hover:bg-blue-600">
                Download QR Code
              </Button>
              <Button onClick={() => router.push('/students')} variant="outline">
                View All Students
              </Button>
              <Button onClick={() => {
                setCurrentStep(1)
                setFormData({
                  fullName: '',
                  personalPhone: '',
                  fatherPhone: '',
                  motherPhone: '',
                  dateOfBirth: '',
                  academicGrade: '',
                  academicStream: '',
                  schoolName: '',
                  selectedInstructors: []
                })
                setQrCodeUrl(null)
                setStudentId(null)
              }} variant="outline">
                Add Another Student
              </Button>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  const canProceed = () => {
    switch (currentStep) {
      case 1:
        return formData.fullName && formData.personalPhone && formData.fatherPhone && 
               formData.motherPhone && formData.dateOfBirth
      case 2:
        return formData.academicGrade && formData.academicStream && formData.schoolName
      case 3:
        return formData.selectedInstructors.length >= 5
      default:
        return false
    }
  }

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-3xl font-bold bg-gradient-to-r from-slate-900 to-slate-600 bg-clip-text text-transparent">
          Student Registration
        </h1>
        <p className="text-slate-600 mt-2">
          Register a new student with comprehensive details and QR code generation
        </p>
      </div>

      {/* Progress Steps */}
      {currentStep < 4 && (
        <div className="flex items-center justify-center space-x-4 mb-8">
          {[1, 2, 3].map((step) => (
            <div key={step} className="flex items-center">
              <div className={`w-10 h-10 rounded-full flex items-center justify-center font-medium ${
                step <= currentStep
                  ? 'bg-blue-500 text-white'
                  : 'bg-slate-200 text-slate-600'
              }`}>
                {step}
              </div>
              {step < 3 && (
                <div className={`w-16 h-1 mx-2 ${
                  step < currentStep ? 'bg-blue-500' : 'bg-slate-200'
                }`} />
              )}
            </div>
          ))}
        </div>
      )}

      {/* Form Content */}
      <Card className="border-0 shadow-xl">
        <CardContent className="p-8">
          {renderStep()}
        </CardContent>
      </Card>

      {/* Navigation Buttons */}
      {currentStep < 4 && (
        <div className="flex justify-between">
          <Button
            variant="outline"
            onClick={() => setCurrentStep(Math.max(1, currentStep - 1))}
            disabled={currentStep === 1}
          >
            Previous
          </Button>
          
          {currentStep < 3 ? (
            <Button
              onClick={() => setCurrentStep(currentStep + 1)}
              disabled={!canProceed()}
              className="bg-blue-500 hover:bg-blue-600"
            >
              Next
            </Button>
          ) : (
            <Button
              onClick={handleSubmit}
              disabled={!canProceed() || isLoading}
              className="bg-green-500 hover:bg-green-600"
            >
              {isLoading ? (
                <div className="flex items-center space-x-2">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                  <span>Registering...</span>
                </div>
              ) : (
                'Complete Registration'
              )}
            </Button>
          )}
        </div>
      )}
    </div>
  )
}
